<template>
  <div class="container">
    <div class="n-header" :class="sticky ? 'fix' : ''">
      <span v-show="sticky">{{ pageTitle }}</span>
      <div class="pannel l" @click="handleBack" v-if="!$route.query.browerFirstIn">
        <span class="back"></span>
      </div>
      <div class="pannel r" @click="handleShare" v-if="$util.isHaierApp() || $util.isSgRn()">
        <span class="share" :style="{backgroundImage:`url(${sticky ?$globalIcon.blackShareIcon: $globalIcon.whiteShareIcon})`}"></span>
      </div>
    </div>
    <div class="content-wrapper" @scroll="handleScroll" ref="toTop">
      <!-- 顶部banner -->
      <div class="top-banner" ref="topBanner">
        <!-- 替换顶部header -->
        <!-- <p>{{ pageTitle }}</p> -->
        <img class="" :src="topImageUrl" />
      </div>
      <!-- 整个下部 -->
      <div class="bottom">
        <div class="center">
          <!-- 导航 -->
          <div class="nav">
            <ul>
              <li
                v-for="(item, index) in cities"
                :key="item.regionId"
                :class="index !== cities.length - 1 ? 'mr' : ''"
                @click="toCity(item)"
              >
                {{ item.regionName }}
              </li>
            </ul>
          </div>
          <!-- 轮播 -->
          <template v-if="topBanners && topBanners.length > 0">
            <lazy-component>
              <van-swipe ref='vanSwiper' class="swipe">
                <van-swipe-item v-for="item in topBanners" :key="item.picNo" @click="handleBannerClick(item)">
                  <img v-lazy="item.pic" />
                </van-swipe-item>
              </van-swipe>
            </lazy-component>
          </template>
        </div>
        <div class="corner">
          <all-product @more="clickMore" :compo-data="recommendProducts"></all-product>
          <Recommend :compo-data="recommendations"></Recommend>
          <Selling :compo-data="hotProducts"></Selling>
          <Quality :compo-data="competitiveProducts"></Quality>
        </div>
      </div>
    </div>
    <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
  </div>
</template>

<script>
import commons from '@/utils/commons.js'
import allProduct from './allProduct/index'
import Recommend from './recommend'
import Selling from './selling'
import Quality from './quality'
// import topFeedBack from '@/components/top-feedBack/index'
import { getProvinceInfo } from '@/http/service/smartChoice.js'
import { mapMutations } from 'vuex'
import mixin from '../../goods/detail/components/mixin.js'
import feedBackMixins from '@/components/top-feedBack/mixins_inpage_scroll.js'
export default {
  mixins: [mixin, feedBackMixins],
  components: {
    allProduct,
    Recommend,
    Selling,
    Quality,
    // topFeedBack,
  },
  data() {
    return {
      regionId: '', //
      sticky: false, // 是否吸顶
      topImageUrl: '',
      pageTitle: '',
      cities: [],
      topBanners: [],
      recommendProducts: [],
      recommendations: [],
      hotProducts: [],
      competitiveProducts: [],
      shareId: '',
      scrollContentTop: 0,
      RootEM: 0,
      scrollTarget: '.content-wrapper', // 页面滚动元素
      // showToTopFlag: false
    }
  },
  mounted() { commons.swipeResize(this, 'vanSwiper') },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
    userId() {
      return this.$store.getters['mId']
    },
  },
  methods: {
    ...mapMutations('smartChoice', ['storeScrollEleTop']),
    getShareId() {
      const { query } = this.$route
      if (query.shareId) this.shareId = query.shareId
      if (query.regionId) {
        this.regionId = query.regionId
        localStorage.setItem('regionId', this.regionId) // 缓存
      }
    },
    prepareShareId() {
      let target = `${location.origin + location.pathname}?regionId=${this.regionId}`
      if (this.userId) target = `${target}&shareId=${this.userId}`
      return target
    },
    handleScroll(e) {
      this.scrollContentTop = this.$refs.toTop.scrollTop
      this.sticky = this.$refs.toTop.scrollTop > this.RootEM * 0.92
      this.showToTopFlag = this.scrollContentTop > 200
    },
    // /**
    //  * 返回顶部
    // */
    // fnScrollToTop() {
    //   this.$refs.topBanner.scrollIntoView({
    //     block: 'start',
    //     behavior: 'smooth', // 平滑过渡
    //   }) // 回到顶部
    // },
    handleBack() {
      this.$store.dispatch('doHistoryBack') // 封装的back方法
    },
    handleShare() {
      // console.log(this.prepareShareId())
      // 分享方法
      this.$util
        .inAppShare({
          title: this.pageTitle,
          // desc: ,
          url: this.prepareShareId(),
          // thumImage: ,
        })
        .then(res => {
          console.log(res)
        })
    },
    toCity(item) {
      this.$router.push({ name: 'smartChoiceCity', query: { title: item.regionName, id: item.regionId } })
    },
    clickMore() {
      this.$router.push({ name: 'smartChoiceProvinceProducts', query: { regionId: this.regionId } })
    },
    handleBannerClick(item) {
      this.$store.dispatch('bannerClick', { hyperLinkType: item.linkType + '', hyperLink: item.link, relationId: item.relationId })
    },
    /**
     *  @description 请求主页数据
     */
    getProvinceInfo() {
      let params = {
        regionId: this.regionId,
        streetId: this.location.townshipId,
      }
      getProvinceInfo(params).then(res => {
        if (res.success) {
          Object.keys(res.data).forEach(k => {
            // console.log(k)
            if (
              [
                'topImageUrl', // 头图
                'pageTitle', // 大标题
                'cities', // 子场馆
                'topBanners', // 头部轮播
                'recommendProducts', // 推荐商品
                'recommendations', // 馆长推荐
                'hotProducts', // 热卖
                'competitiveProducts', // 精品
              ].includes(k)
            ) {
              this[k] = res.data[k]
            }
          })
        } else {
          this.$toast(res.message)
        }
      })
    },
    initData() {
      this.getShareId()
      setTimeout(() => {
        this.getProvinceInfo() // 确保从缓存中取出数据
      })
    }
  },
  created() {
    // this.initData()
    this.RootEM = this.getREM()
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (from.name === 'specialtyStore') {
        vm.regionId = to.query.regionId
        localStorage.setItem('regionId', to.query.regionId) // 缓存一下
      }
      if (!from.name) {
        vm.regionId = localStorage.getItem('regionId')
        // vm.initData()
      }
      vm.initData()
    })
  },
  beforeRouteLeave(to, from, next) {
    if (to.name !== 'specialtyStore') {
      this.$store.commit('storeScrollEleTop', this.scrollContentTop)
    } else {
      this.$store.commit('storeScrollEleTop', 0)
    }
    next()
  },
  activated () {
    this.$refs.toTop.scrollTop = this.$store.state.smartChoice.scrollEleTop
  }
}
</script>

