<template>
  <!-- 全部商品 -->
  <div class="all-product" v-if="compoData && compoData.length > 0">
    <!-- title -->
    <h3>
      {{ compoData[0].moudleTitle }}
      <span @click="$emit('more')">更多</span>
    </h3>
    <!-- list -->
    <div class="product-list">
      <lazy-component>
        <div
          class="item"
          v-for="(item, index) in list"
          :key="item.id"
          :class="index % 2 === 0 ? 'mr' : ''"
          @click="$router.push({ path: '/goodsDetail', query: { productId: item.id } })"
        >
          <img class="p-pic" v-lazy="item.imageUrl" />
          <!-- <p class="p-desc">
            {{ item.name | clipText }}
          </p>
          <p class="p-price">￥{{ item.price }}</p> -->
        </div>
      </lazy-component>
    </div>
  </div>
</template>

<script>
import mixin from '../../components/mixin'
export default {
  mixins: [mixin],
  props: {
    compoData: {
      type: Array,
    },
  },
  computed: {
    list() {
      if (this.compoData.length > 4) return this.compoData.slice(-4)
      return this.compoData
    },
  },
  data() {
    return {}
  },
}
</script>

