<template>
  <div class="drinks" v-if="compoData && compoData.length > 0">
    <h3>
      {{ compoData[0][0].moduleTitle }}
    </h3>
    <lazy-component>
      <van-swipe class="swiper_wrap">
        <van-swipe-item v-for="(item, index) in compoData" :key="index">
          <div class="center">
            <img v-for="innerItem in item" :key="innerItem.picNo" v-lazy="innerItem.pic" @click="handleBannerClick(innerItem)" />
          </div>
        </van-swipe-item>
      </van-swipe>
    </lazy-component>
  </div>
</template>

<script>
export default {
  props: {
    compoData: {
      type: Array,
    },
  },
  methods: {
    handleBannerClick(item) {
      this.$store.dispatch('bannerClick', { hyperLinkType: item.linkType + '', hyperLink: item.link, relationId: item.relationId })
    },
  },
}
</script>

<style scoped>
.drinks {
  margin-bottom: 0.32rem;
}
.center {
  overflow: hidden;
}
.center img {
  float: left;
  width: 50%;
  height: 2.16rem;
  object-fit: cover;
}
.center img:nth-of-type(1) {
  height: 4.32rem;
}
.swiper_wrap{
  border-radius: 0.16rem;;
}
</style>
